import React from "react";

import "./RadioInput.css";

const RadioInput = ({ inputOptions, optionsName, onClick }) => {
  return (
    <div className="radio-inputs">
      <label className="radio-inputs__title">{optionsName}</label>
      <hr />
      {inputOptions.map((option) => {
        return (
          <label key={option.id} className="radio">
            <input 
              type="radio" 
              name={option.name} 
              value={option.value}
              onChange={(e) => onClick(option.value, true)} 
            />
            <span className={`radio-span ${option.className}`}>
              {option.label}
            </span>
          </label>
        );
      })}
    </div>
  );
};

export default RadioInput;
