import React, { useState, useRef, useEffect } from 'react';
import { FaSearch, FaTimes } from 'react-icons/fa';
import './UserSelector.css';

const UserSelector = ({ users, selectedUsers, onSelectUsers, label, multiple = false }) => {
  const [isOpen, setIsOpen] = useState(false);
  const [searchTerm, setSearchTerm] = useState('');
  const dropdownRef = useRef(null);
  const inputRef = useRef(null);

  useEffect(() => {
    const handleClickOutside = (event) => {
      if (dropdownRef.current && !dropdownRef.current.contains(event.target)) {
        setIsOpen(false);
      }
    };

    document.addEventListener('mousedown', handleClickOutside);
    return () => document.removeEventListener('mousedown', handleClickOutside);
  }, []);

  const filteredUsers = users.filter(user => 
    !selectedUsers.some(selected => selected.id === user.id) &&
    (user.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
     user.email.toLowerCase().includes(searchTerm.toLowerCase()))
  );

  const handleSelect = (user) => {
    if (multiple) {
      onSelectUsers([...selectedUsers, user]);
    } else {
      onSelectUsers([user]);
    }
    setSearchTerm('');
    setIsOpen(false);
  };

  const handleRemove = (userId) => {
    onSelectUsers(selectedUsers.filter(user => user.id !== userId));
  };

  return (
    <div className="user-selector-container">
      {label && <label>{label}</label>}
      
      <div className="user-selector-input">
        <div className="input-wrapper">
          <FaSearch className="search-icon" />
          <input
            ref={inputRef}
            type="text"
            placeholder="Search users..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            onFocus={() => setIsOpen(true)}
          />
          {searchTerm && (
            <button 
              className="clear-button"
              onClick={() => setSearchTerm('')}
            >
              <FaTimes />
            </button>
          )}
        </div>

        {isOpen && (
          <div className="user-selector-dropdown" ref={dropdownRef}>
            {filteredUsers.length > 0 ? (
              filteredUsers.map(user => (
                <div
                  key={user.id}
                  className="user-option"
                  onClick={() => handleSelect(user)}
                >
                  <img
                    src={user.image || '/images/default-avatar.png'}
                    alt={user.name}
                    className="user-avatar"
                  />
                  <div className="user-info">
                    <div className="user-name">{user.name}</div>
                    <div className="user-email">{user.email}</div>
                  </div>
                </div>
              ))
            ) : (
              <div className="no-users-message">
                {searchTerm ? 'No users found' : 'Start typing to search users'}
              </div>
            )}
          </div>
        )}
      </div>

      {selectedUsers.length > 0 && (
        <div className="selected-users">
          {selectedUsers.map(user => (
            <div key={user.id} className="selected-user">
              <img
                src={user.image || '/images/default-avatar.png'}
                alt={user.name}
                className="user-avatar"
              />
              <span>{user.name}</span>
              <button onClick={() => handleRemove(user.id)}>
                <FaTimes />
              </button>
            </div>
          ))}
        </div>
      )}
    </div>
  );
};

export default UserSelector;
